<template>
  <div class="jxServiceTab">
     <div class="page-navbar">
      <mt-navbar class="page-part" v-model="selected">
        <mt-tab-item v-for="navbar in navbars" :id="navbar">{{navbar}}</mt-tab-item>
      </mt-navbar>
      <div class="jxServiceTabBody">
        <mt-tab-container v-model="selected">
           <mt-tab-container-item v-for="container in containers"  :id="container.title">
            <div v-for="(clg, index) in container.catalog" @click="handleClick(container.id,clg.title,clg.price,clg.duration)" :class="index % 2 == 0 ? 'blue servicesListItem' : 'red servicesListItem' ">
              <p>{{clg.title}}</p>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>
    </div>
  </div>
</template>

<script>

import {mapGetters, mapActions} from 'vuex'
export default {
  name: 'jxServicesTab',
  data() {
    return {
      selected: ''
    };
  },
  computed: {
    ...mapGetters({
       globalVal: 'getGlodalVal',
       navbars: 'getNavbars',
       containers: 'getContainers'
    })
  },
  methods: {
    ...mapActions({
      fetchJxServicesList: 'fetchJxServicesList',
      changeCompanyType: 'changeCompanyType',
      changeServiceIndex: 'changeServiceIndex'
    }),
    handleClick: function(id, title, price,duration) {
      const serviceIndex = [];
      this.changeCompanyType(title + ' ' + price + '元');
      this.navbars.forEach((item, index) => {
        if(item == this.selected) {
           serviceIndex.push(index);
        }
      })
      this.containers.forEach(i => {
        if(i.title == this.selected) {
          i.catalog.forEach((item,index) => {
            if(item.title == title) {
              serviceIndex.push(index);
            }
          })
        }
      });
      this.changeServiceIndex(serviceIndex);
      this.$router.push({path: '/appointment', query: { id: id, type: 'jx', duration: duration }})
    }
  },
  created() {
    this.fetchJxServicesList(this.globalVal.currentCity).then(response => {
        this.selected = this.navbars[0];
      }).catch(error => {
            if(error.body.code == 3001) {
              this.$router.push('/login');
            }
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.jxServiceTab {
 background-color: #efeeef;
 height: 100%;
}
.jxServiceTabBody {
  padding: 0 0.3rem;
}
.jxServiceTab .mint-navbar .mint-tab-item.is-selected {
    border-bottom: 4px solid #ff6990;
    color: #565256;
    margin-bottom: -3px;
}
.jxServiceTab .mint-tab-item .mint-tab-item-label {
  font-size: 0.4rem !important;
}
.jxServiceTab .servicesListItem {
  font-size: 0.4rem;
  color: #565256;
  display: flex;
  display: -ms-flexbox;
  justify-content: center;
  align-items: center;
  height: 2rem;
  background-color: #fff;
  margin: 0.3rem 0;
}
.jxServiceTab .servicesListItem.red {
  border-left: 0.3rem solid #ff6990;
}
.jxServiceTab .servicesListItem.blue {
  border-left: 0.3rem solid #559cf4;
}
</style>



